// zarovnn textu
.center {
  text-align: center;
}
.left {
  text-align: left;
}
.right {
  text-align: right;
}
// nezalamovn
.nowrap {
  white-space: nowrap;
}
// floatovn
.fleft {
  float: left;
}
.fright {
  float: right;
}
.cls {
  clear: both;
}
// stavy
.is-visible {
  display: block;
}
.is-open {
  display: block;
}
.is-hidden {
  display: none;
}
// micro clearfix ( http://nicolasgallagher.com/micro-clearfix-hack/ )
.cf:before,
.cf:after {
  content: ' ';
  display: table;
}
.cf:after {
  clear: both;
}
.cf {
  *zoom: 1;
}
// Pidv se na body pi zobrazen modlnch oken. Zamez scrollovn obsahu na pozad modalu.
.is-unscrollable {
  overflow: hidden;
}
.m-0 {
  margin-bottom: 0;
}
