
.icon--icon-arrow-left {
  width: 46px;

  &:before {
    padding-top: calc(46 / 46) * 100%;
  }
}

.icon--icon-arrow-right {
  width: 46px;

  &:before {
    padding-top: calc(46 / 46) * 100%;
  }
}

.icon--icon-times {
  width: 16px;

  &:before {
    padding-top: calc(16 / 16) * 100%;
  }
}

.icon--icon-x {
  width: 35px;

  &:before {
    padding-top: calc(34 / 35) * 100%;
  }
}
