// zkladn vzhled input pol
input,
textarea,
select {
  padding: 7px 10px;
  color: $color_black;
  font-size: 16px;
  line-height: 16px; // pro sladn vky
  border: 1px solid $color_main;
  border-radius: $radius;
  background-color: $color_white;
  -webkit-appearance: none;
  &::placeholder {
    color: $color_main_dark;
    opacity: 1;
  }
  &:focus {
    border-color: $color_main_dark;
    outline: 0;
  }
  &.is-success {
    border-color: $color_success;
  }
  &.is-danger {
    border-color: $color_danger;
  }
}

// pravy pro textarea
textarea {
  line-height: 1.4; // itelnj rozestupy mezi dky
}

// popisek pole
label {
  display: inline-block;
  font-weight: 700;
}

// oeten submit inputu, pokud je pouit
button,
input[type='submit'] {
  width: auto;
  cursor: pointer;
}

// oeten chekboxu a radio buttonu
input[type='checkbox'],
input[type='radio'] {
  width: auto;
}
