// chybov hlky
.alert {
  margin: 10px 0;
  padding: 12px 15px;
  color: $color_white;
  border-radius: $radius;
  background-color: $color_main;
  @media (min-width: $mqsm) {
    padding: 17px 20px;
  }
}
// potvrzujc hlka
.alert--success {
  background-color: $color_success;
}
// informan hlka
.alert--info {
  background-color: $color_info;
}
// vstran hlka
.alert--danger {
  background-color: $color_danger;
}
