// hlavn kontejner
.container {
  width: 100%;
  max-width: $container_main;
  margin: 0 auto;
  padding: 0 $layout_gap;
}
.container--lg {
  max-width: 1800px;
}
.container--full {
  max-width: 100%;
}
.container--text {
  max-width: 960px;
}
// dek mky
.row {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 (-$layout_gap);
  // mezera mezi boxy
  & > .col {
    padding: $layout_gap;
  }
}
// fix pro max-width tabulku
.row--block {
  @media (max-width: $mqsm) {
    display: block;
  }
}
// dek se stejn vysokmi bloky
.row--align {
  & > .col {
    flex-direction: row;
  }
}
// mezery mezi bloky
.row--space {
  justify-content: space-between;
}
// centrovn
.row--center {
  align-items: center;
}
// dek bez mezer
.row--nogap {
  margin: 0;
  & > .col {
    padding: 0;
  }
}
// blok mky
.col {
  flex: 1 0 100%;
  display: flex;
  flex-direction: column;
}
// dva vedle sebe
.col--2 {
  @media (min-width: $mqxs) {
    flex-basis: auto;
    width: 50%;
  }
}
// ti vedle sebe
.col--3 {
  @media (min-width: $mqxs) {
    flex-basis: auto;
    width: 50%;
  }
  @media (min-width: $mqmd) {
    width: 33.33333333%;
  }
}
// tyi vedle sebe
.col--4 {
  @media (min-width: $mqxs) {
    flex-basis: auto;
    width: 50%;
  }
  @media (min-width: $mqmd) {
    width: 25%;
  }
}
// pt vedle sebe
.col--5 {
  @media (min-width: $mqxs) {
    flex-basis: auto;
    width: 50%;
  }
  @media (min-width: $mqmd) {
    width: 20%;
  }
}
// est vedle sebe
.col--6 {
  @media (min-width: $mqxs) {
    flex-basis: auto;
    width: 50%;
  }
  @media (min-width: $mqmd) {
    width: 16.66666666666667%;
  }
}
// buka se roztahuje
.col--grow {
  flex-grow: 1;
}
// zarovnn na pravou stranu
.col--end {
  align-items: flex-end;
}
// zarovnn na levou stranu
.col--start {
  align-items: flex-start;
}
// zarovnn na sted
.col--center {
  align-items: center;
}
// zmna osy
.col--row {
  flex-direction: row;
}
